body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.sign{
  font-family:Tangerine, serif ;
  font-size: 2rem;
  letter-spacing: 3px;
  color: goldenrod;
  text-align: center;
  text-shadow: 2px 3px 3px #aaa;
  
}
.runner{

	display:flex;
	justify-content: center;
	align-items: center;
	
	box-sizing:border-box;
	padding-top: 128px !important;
	   /* padding-bottom: 128px !important */
	}
	.runner .animated-txt{
	  line-height:90px;
	  height: 90px;
	  overflow: hidden;
	}
	 .animated-txt li{
	  color: #ffdfa4 !important;
	list-style: none;
	font-size: 56px;
	font-weight: 500;
	top: 0;
	position: relative;
	animation: slide 12s steps(4) infinite;
	 }
	 @keyframes slide{
	  100%{
		top:-360px
	  }
	 }
	
	.animated-txt li{
	  position: relative;
	}
	
	.animated-txt li::after{
	  content:"";
	  position:absolute;
	  left:0;
	  height: 100%;
	  width: 100%;
	  background-color: black;
	  border-left: 5px solid rgb(168, 168, 168);
	  animation:typing 3s steps(40) infinite;
	}
	
	@keyFrames typing{
	  30%,
	  50%{
		left:calc(100% + 30px);
	  }
	  100%{
		left:0;
	  }
	}
	.global-text{
	  display:flex;
	  justify-content: center;
	  align-items: center;
	  font-size: 18px;
	  line-height: 32px;
	  color: #333e48 !important;
	  font-weight: 500;
	}

